import { RefObject, useCallback, useEffect, useRef, useState } from 'react';
import ReactMarkdown from 'react-markdown'
import remarkGfm from 'remark-gfm';
import rehypeRaw from 'rehype-raw';

export type Image = {
    alt?: string
    src?: string
    title?: string
    maxWidth?: number
}

export function ResponsiveImage({ alt, src, title, maxWidth }: Image) {
    return <img alt={alt} src={src} title={title} style={{ maxWidth: maxWidth }} />
}

export function ResponsiveIframe({ src, title, maxWidth }: Image) {
    const height = maxWidth! / (4 / 3)

    return <iframe title={title} src={src} style={{ maxWidth: maxWidth }} width={maxWidth} height={height} />
}

export const useResizeOnLoad = (elementRef: RefObject<HTMLDivElement>, onProps?: any) => {
    const getWidth = useCallback(() => elementRef?.current?.offsetWidth, [elementRef])
    const [width, setWidth] = useState<number | undefined>(undefined);

    useEffect(() => {
        const handleResize = () => setWidth(getWidth())

        if (elementRef.current) {
            setWidth(getWidth)
        }

        window.addEventListener('resize', handleResize)

        return () => window.removeEventListener('resize', handleResize)
    }, [elementRef, getWidth, onProps])

    return width

}

export const useResize = (elementRef: RefObject<HTMLDivElement>) => {
    const getWidth = useCallback(() => elementRef?.current?.offsetWidth, [elementRef])
    const [width, setWidth] = useState<number | undefined>(undefined);

    useEffect(() => {
        const handleResize = () => setWidth(getWidth())

        if (elementRef.current) {
            setWidth(getWidth)
        }

        window.addEventListener('resize', handleResize)

        return () => window.removeEventListener('resize', handleResize)
    }, [elementRef, getWidth])

    return width
}

export default function Markdown({ input }: { input: any }) {
    const elementRef = useRef<HTMLDivElement>(null)
    const maxWidth = useResizeOnLoad(elementRef, input)

    return (
        <div className="markdown-content">
            <div className="markdown-component" ref={elementRef}>
                <ReactMarkdown
                    children={input}
                    remarkPlugins={[remarkGfm]}
                    rehypePlugins={[rehypeRaw]}
                    components={{
                        img: ({ ...props }) => <ResponsiveImage maxWidth={maxWidth} {...props} />,
                        iframe: ({ ...props }) => <ResponsiveIframe maxWidth={maxWidth} {...props} />
                    }}
                />
            </div>
        </div>
    )
}